import AppUrl from '../../api/baseApi.js'
export default {
	data() {
		return {
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			"banner_index": [{
				"img": "",
				"type": "",
				"param": ""
			}],
		}
	},
	onLoad() {
		var _self = this;
		const requestTask1 = uni.request({
			url: AppUrl.HOME_INDEX,
			success: (res) => {
				_self.banner_index = res.data.data.banner_index;

			}
		});
	},
	methods: {
		onItemClick: function(options) {

			switch (options.type) {
				case 'report': //跳转到数据模块
					break;
				case 'aggongxu':
					break;
				case 'report': //跳转到供需主页
					break;
				case 'h5': //跳转到活动页面
					uni.navigateTo({
						url: '../web/common-webview/common-webview?webUrl=' + options.param
					})
					break;
				default:
					break;
			}

		}
	}
}
